.navbar{
     height: 50px;
     border-bottom: 0.5px solid rgb(231,228,228) ;
     display: flex;
     align-items: center;
     font-size: 14px;
     color:#555;
     .wrapper{
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: space-between;
     
          .search{
               display: flex;
               align-items: center;
               border: .5px solid lightgray;
               padding: 3px;
               margin-left: 20px;
               input{
                    border: none;
                    outline: none;
                    background-color: transparent;
                    &::placeholder{
                         font-size: 12px;
                    }
               }
          }
          .items{
               display: flex;
               align-items: center;
               .item{
                    display: flex;
                    align-items: center;
                    margin-right: 20px;
                    position: relative;
                    cursor: pointer ;
                    .icon{
                         font-size: 20px;
                    }
                    .avatar{
                         border-radius: 50%;
                         height: 30px;
                         width:30px;
                    }
                    .counter{
                         width: 15px;
                         height: 15px;
                         background-color: red;
                         color: white ;
                         font-size: 10px;
                         font-weight: bold;
                         border-radius: 50%;
                         display: flex;
                         align-items: center;
                         justify-content: center;
                         position: absolute;
                         top: -5px;
                         right: -5px;

                    }
               }
               
               
          }
     }
}