.sidebar{
     flex: 1;
     border-right: 0.5px solid rgb(230, 227, 227);
     min-height: 100vh;
     .top{
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
          .logo{
               font-size:20px ;
               font-weight: bold;
               color: #6439ff ;
          }
     }
     hr{
          height:0;
          border: 0.5px solid rgb(230, 227, 227);
     }
     .center{
          padding-left: 10px;
          ul{
               list-style: none;
               margin: 0;
               padding: 0;
               .title{
                    font-size: 10px;
                    font-weight: bold;
                    color:#999;
                    margin-top: 15px;
                    margin-bottom: 5px;
               }
               li{
                    display: flex;
                    align-items: center;
                    padding: 5px;
                    cursor: pointer;

                   

                    .icon{
                         font-size: 18px;
                         color: #7451f8;
                    }
                    span{
                         font-size: 16px;
                         font-weight: 600;
                         color: #888;
                         margin-left: 10px;
                    }

                    &:hover{
                         background-color: #ece8ff;
               
                    }
               }
          
          }
     } 
     .bottom{
          display: flex;
          align-items: center;
          margin: 10px;
          .colorOptions{
               height: 20px;
               width: 20px ;
               border: 0.5px solid #7451f8;
               border-radius: 5px;
               margin: 5px;
               cursor: pointer;

               &:nth-child(1){
                    background-color: whitesmoke;
               }
               &:nth-child(2){
                    background-color: #333;
               }
               &:nth-child(3){
                    background-color: darkblue;
               }
          }
     }
}